// Variables must be imported before this file
@use './../config/variables' as *;
@use './../config/mixins' as *;
@use 'sass:map';
@use 'sass:color';

@mixin generate-utilities($breakpoint-suffix: '') {
  @each $key, $val in $overflows {
    .overflow#{$breakpoint-suffix}-#{$key} {
      overflow: #{$val} !important;
    }
  }
  @each $key, $val in $displays {
    .d#{$breakpoint-suffix}-#{$key} {
      display: $val !important;
    }
  }
  @each $key, $val in $positions {
    .position#{$breakpoint-suffix}-#{$key} {
      position: $val !important;
    }
  }
  @each $key, $val in $gap-sizes {
    .gap#{$breakpoint-suffix}-#{$key} {
      gap: $val !important;
    }
    .gap-x#{$breakpoint-suffix}-#{$key} {
      column-gap: $val !important;
    }
    .gap-y#{$breakpoint-suffix}-#{$key} {
      row-gap: $val !important;
    }
  }
  @each $key, $val in $flex-directions {
    .flex#{$breakpoint-suffix}-#{$key} {
      flex-direction: $val !important;
    }
  }
  @each $key, $value in $border-radius {
    @if $key == 'default' {
      .border#{$breakpoint-suffix}-rounded {
        border-radius: $value !important;
      }
    } @else {
      .border#{$breakpoint-suffix}-rounded-#{$key} {
        border-radius: $value !important;
      }
    }
  }
  @for $i from 1 through $grid-columns {
    .w#{$breakpoint-suffix}-#{$i} {
      width: calc(100% / $grid-columns * #{$i});
    }
  }
  @each $prefix in (p, m) {
    $applied: 'padding';
    @if $prefix == 'm' {
      $applied: 'margin';
    }
    @each $key, $val in $gap-sizes {
      .#{$prefix}#{$breakpoint-suffix}-#{$key} {
        #{$applied}: $val !important;
      }
      .#{$prefix}t#{$breakpoint-suffix}-#{$key} {
        #{$applied}-top: $val !important;
      }
      .#{$prefix}r#{$breakpoint-suffix}-#{$key} {
        #{$applied}-right: $val !important;
      }
      .#{$prefix}b#{$breakpoint-suffix}-#{$key} {
        #{$applied}-bottom: $val !important;
      }
      .#{$prefix}l#{$breakpoint-suffix}-#{$key} {
        #{$applied}-left: $val !important;
      }
      .#{$prefix}x#{$breakpoint-suffix}-#{$key} {
        #{$applied}-left: $val !important;
        #{$applied}-right: $val !important;
      }
      .#{$prefix}y#{$breakpoint-suffix}-#{$key} {
        #{$applied}-bottom: $val !important;
        #{$applied}-top: $val !important;
      }
    }
  }
}

@include generate-utilities;
@each $name, $size in $mq {
  @media #{$size} {
    @include generate-utilities('-#{$name}');
  }
}

@each $key, $variants in $theme-colors {
  $light-value: map.get($variants, light);
  $dark-value: map.get($variants, dark);
  .bg-#{$key} {
    background-color: var(--color-bg-#{$key});
    background-position: top center;
    background-size: map.get($gap-sizes, 2) map.get($gap-sizes, 2);
    background-image: repeating-linear-gradient(
      45deg,
      var(--color-bg-dot-#{$key}) 0,
      var(--color-bg-dot-#{$key}) 1px,
      var(--color-bg-#{$key}) 0,
      var(--color-bg-#{$key}) 50%
    );
  }
}
@each $key, $variant in $grays {
  .bg-#{$key} {
    background-color: var(--color-bg-#{$key});
    background-position: top center;
    background-size: map.get($gap-sizes, 2) map.get($gap-sizes, 2);
    background-image: repeating-linear-gradient(
      45deg,
      var(--color-bg-dot-#{$key}) 0,
      var(--color-bg-dot-#{$key}) 1px,
      var(--color-bg-#{$key}) 0,
      var(--color-bg-#{$key}) 50%
    );
  }
}
