@use './../config/functions' as *;
@use './../config/mixins' as *;
@use './../config/variables' as *;
@use 'sass:map';

html {
  @include noto-sans;
  font-size: 16px;
  line-height: 1.25;
}

.display-1 {
  @include display1;
}

.display-2 {
  @include display2;
}

.display-3 {
  @include display3;
}

.display-4 {
  @include display4;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6,
label {
  margin: 0 0 0.75em 0;
}

h1,
.h1 {
  @include h1;
}

h2,
.h2 {
  @include h2;
}

h3,
.h3 {
  @include h3;
}

h4,
.h4 {
  @include h4;
}

h5,
.h5 {
  @include h5;
}

h6,
.h6 {
  @include h6;
}

p {
  @include para;
}

p,
.para {
  @include para2;
}

label {
  @include label;
}

small,
.small {
  @include small;
}

input,
select,
textarea,
button {
  @include inputs;
}

pre,
code,
kbd,
samp {
  @include mono;
}

li {
  margin: 1em 0;
}
