@use './../config/variables' as *;
@use './../config/mixins' as *;
@use './../config/functions' as *;
@use 'sass:map';
@use 'sass:color';

:root {
  @each $key, $variants in $theme-colors {
    $light-value: map.get($variants, light);
    $dark-value: map.get($variants, dark);
    .light-mode {
      --color-btn-s-bg-#{$key}: #{$light-value};
      --color-btn-s-text-#{$key}: #{color.adjust($dark-value, $lightness: 50%)};
      --color-btn-s-border-#{$key}: #{color.adjust($light-value, $lightness: -10%)};
      --color-btn-s-bg-hover-#{$key}: #{color.adjust($light-value, $lightness: -10%)};
      --color-btn-s-text-hover-#{$key}: #{color.adjust($dark-value, $lightness: 50%)};

      --color-btn-o-bg-#{$key}: #{rgba($light-value, 17.5%)};
      --color-btn-o-text-#{$key}: #{color.adjust($light-value, $lightness: -10%)};
      --color-btn-o-border-#{$key}: #{color.adjust($light-value, $lightness: -10%)};
      --color-btn-o-bg-hover-#{$key}: #{color.adjust($light-value, $lightness: -10%)};
      --color-btn-o-text-hover-#{$key}: #{color.adjust($dark-value, $lightness: 50%)};
    }
    .dark-mode {
      --color-btn-s-bg-#{$key}: #{$dark-value};
      --color-btn-s-text-#{$key}: #{color.adjust($light-value, $lightness: -50%)};
      --color-btn-s-border-#{$key}: #{color.adjust($dark-value, $lightness: 10%)};
      --color-btn-s-bg-hover-#{$key}: #{color.adjust($dark-value, $lightness: 10%)};
      --color-btn-s-text-hover-#{$key}: #{color.adjust($light-value, $lightness: -50%)};

      --color-btn-o-bg-#{$key}: #{rgba($dark-value, 17.5%)};
      --color-btn-o-text-#{$key}: #{color.adjust($dark-value, $lightness: 10%)};
      --color-btn-o-border-#{$key}: #{color.adjust($dark-value, $lightness: 10%)};
      --color-btn-o-bg-hover-#{$key}: #{color.adjust($dark-value, $lightness: 10%)};
      --color-btn-o-text-hover-#{$key}: #{color.adjust($light-value, $lightness: -50%)};
    }
  }
}

.btn.disabled,
.btn:disabled,
fieldset:disabled .btn {
  pointer-events: none;
  opacity: 0.5;
}

.btn {
  @include noto-sans;
  align-items: center;
  background-color: transparent;
  border-color: transparent;
  border-radius: map.get($map: $border-radius, $key: default);
  border-style: solid;
  border-width: var(--border-width);
  color: inherit;
  cursor: pointer;
  display: inline-flex;
  font-size: px-to-rem(16px);
  font-weight: 400;
  gap: map.get($gap-sizes, 4);
  justify-content: space-between;
  line-height: inherit;
  padding: map.get($gap-sizes, 2) map.get($gap-sizes, 3);
  text-align: center;
  text-decoration: none;
  user-select: none;
  vertical-align: middle;
  .icon-gm {
    letter-spacing: 0.125em;
    padding-left: 0.125em;
  }
  @include bpxl {
    font-size: px-to-rem(18px);
    padding: map.get($gap-sizes, 3) map.get($gap-sizes, 4);
  }
  &.btn-icon-only {
    justify-content: center;
    padding: map.get($gap-sizes, 2);
    @include bpxl {
      padding: map.get($gap-sizes, 3);
    }
  }
  &.btn-sm {
    border-radius: map.get($map: $border-radius, $key: sm);
    font-size: px-to-rem(14px);
    padding: map.get($gap-sizes, 1) map.get($gap-sizes, 2);
    &.btn-icon-only {
      padding: map.get($gap-sizes, 1);
    }
    @include bpxl {
      padding: map.get($gap-sizes, 2) map.get($gap-sizes, 3);
      &.btn-icon-only {
        padding: map.get($gap-sizes, 2);
      }
    }
  }
  &.btn-lg {
    border-radius: map.get($map: $border-radius, $key: lg);
    font-size: px-to-rem(22px);
    padding: map.get($gap-sizes, 3) map.get($gap-sizes, 4);
    &.btn-icon-only {
      padding: map.get($gap-sizes, 3);
    }
    @include bpxl {
      padding: map.get($gap-sizes, 4) map.get($gap-sizes, 5);
      &.btn-icon-only {
        padding: map.get($gap-sizes, 4);
      }
    }
  }
  &.btn-xl {
    border-radius: map.get($map: $border-radius, $key: xl);
    font-size: px-to-rem(26px);
    padding: map.get($gap-sizes, 4) map.get($gap-sizes, 5);
    &.btn-icon-only {
      padding: map.get($gap-sizes, 4);
    }
    @include bpxl {
      padding: map.get($gap-sizes, 5) map.get($gap-sizes, 6);
      &.btn-icon-only {
        padding: map.get($gap-sizes, 5);
      }
    }
  }
  &.btn-pill {
    border-radius: px-to-rem(128px) !important;
  }
}

@each $key, $variants in $theme-colors {
  .btn.btn-solid-#{$key} {
    $this-background-color: var(--color-btn-s-bg-#{$key});
    $this-border-color: var(--color-btn-s-border-#{$key});
    $this-hover-bg-color: var(--color-btn-s-bg-hover-#{$key});
    $this-hover-text-color: var(--color-btn-s-text-hover-#{$key});
    $this-text-color: var(--color-btn-s-text-#{$key});

    @include button-variant(
      $backgroundColor: $this-background-color,
      $borderColor: $this-border-color,
      $textColor: $this-text-color,
      $hoverBackgroundColor: $this-hover-bg-color,
      $hoverTextColor: $this-hover-text-color
    );
  }
  .btn.btn-outline-#{$key} {
    $this-background-color: var(--color-btn-o-bg-#{$key});
    $this-border-color: var(--color-btn-o-border-#{$key});
    $this-hover-bg-color: var(--color-btn-o-bg-hover-#{$key});
    $this-hover-text-color: var(--color-btn-o-text-hover-#{$key});
    $this-text-color: var(--color-btn-o-text-#{$key});
    @include button-variant(
      $backgroundColor: $this-background-color,
      $borderColor: $this-border-color,
      $textColor: $this-text-color,
      $hoverBackgroundColor: $this-hover-bg-color,
      $hoverTextColor: $this-hover-text-color
    );
  }
}

@each $key, $variant in $brands {
  .light-mode .btn.btn-solid-#{$key} {
    @include button-variant(
      $backgroundColor: $variant,
      $borderColor: $variant,
      $textColor: map.get($grays, 'gray-0'),
      $hoverBackgroundColor: color.adjust($variant, $lightness: -10%),
      $hoverTextColor: map.get($grays, 'gray-0')
    );
  }
  .dark-mode .btn.btn-solid-#{$key} {
    @include button-variant(
      $backgroundColor: $variant,
      $borderColor: $variant,
      $textColor: map.get($grays, 'gray-0'),
      $hoverBackgroundColor: color.adjust($variant, $lightness: 10%),
      $hoverTextColor: map.get($grays, 'gray-0')
    );
  }
}

a,
.btn {
  color: var(--color-link);
  &:hover {
    color: var(--color-link-hover);
  }
}
