@use './../config/variables' as *;
@use './../config/mixins' as *;
@use 'sass:map';

// --- Mixins for Grid Structure ---

// Mixin to define the base styles for a flex grid row.
// It uses negative margins to counteract the column padding, ensuring the content aligns to the grid's edges.
@mixin make-row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc($gutter-mobile / -2);
  margin-right: calc($gutter-mobile / -2);
  width: calc(100% + #{$gutter-mobile});
  // Adjust negative margins for larger devices (from the 'md' breakpoint)

  @media #{map.get($mq, 'xl')} {
    margin-left: calc($gutter-desktop / -2);
    margin-right: calc($gutter-desktop / -2);
    width: calc(100% + #{$gutter-desktop});
  }
}

// Mixin to define the base styles for a flex grid column.
// It sets default width to 100% for mobile-first and applies padding for gutters.
@mixin make-col-base {
  width: 100%; // Default to full width for mobile-first, overridden by .col-{breakpoint}-{span} classes
  flex-shrink: 0; // Prevent columns from shrinking below their content size
  flex-basis: auto; // Allow flex basis to be determined by width

  // Apply mobile gutter padding by default
  padding-left: calc($gutter-mobile / 2);
  padding-right: calc($gutter-mobile / 2);

  // Adjust padding for larger devices (from the 'md' breakpoint)

  @media #{map.get($mq, 'xl')} {
    padding-left: calc($gutter-desktop / 2);
    padding-right: calc($gutter-desktop / 2);
  }
}

// Mixin to generate all column width and offset classes for a given breakpoint.
// E.g., for breakpoint '-md', it generates classes like .col-md-6, .offset-md-3.
// @param $breakpoint-suffix: A string suffix for the breakpoint (e.g., '', '-sm', '-md').
@mixin generate-column-classes($breakpoint-suffix: '') {
  // Generate .col-{suffix}-{span} classes for column widths
  .col#{$breakpoint-suffix}-auto {
    flex: 0 0 auto;
    width: auto;
  }
  @for $i from 1 through $grid-columns {
    .col#{$breakpoint-suffix}-#{$i} {
      flex: 0 0 auto; // Prevent growing/shrinking, ensure explicit width
      max-width: 100%; // Ensure it doesn't overflow its container
      width: calc(100% / $grid-columns * #{$i}); // Calculate width based on span
    }
  }

  // Generate .offset-{suffix}-{value} classes for column offsets
  @for $i from 0 through ($grid-columns - 1) {
    // Offset can be 0 (no offset) up to grid-columns-1
    .offset#{$breakpoint-suffix}-#{$i} {
      margin-left: calc(100% / $grid-columns * #{$i}); // Calculate margin-left for offset
    }
  }
}

// --- Mixins for Order Utilities ---

// Generates flexbox `order` utility classes for changing the visual order of flex items.
// @param $breakpoint-suffix: A string suffix for the breakpoint (e.g., '', '-sm', '-md').
@mixin generate-order-classes($breakpoint-suffix: '') {
  // Generate .order-{suffix}-{value} classes
  @for $i from 0 through $grid-columns {
    // Order values typically from 0 to grid-columns-1, plus first/last
    .order#{$breakpoint-suffix}-#{$i} {
      order: $i;
    }
  }
  // Special order values for first and last
  .order#{$breakpoint-suffix}-first {
    order: -1;
  }
  .order#{$breakpoint-suffix}-last {
    order: $grid-columns + 1; // A value higher than any possible column order
  }
}

@mixin generate-flex-grow($breakpoint-suffix: '') {
  // Generate .order-{suffix}-{value} classes
  @for $i from 0 through $grid-columns {
    // Order values typically from 0 to grid-columns-1, plus first/last
    .flex-grow#{$breakpoint-suffix}-#{$i} {
      flex-grow: $i;
    }
  }
}

// --- Mixins for Alignment Utilities ---

// Generates flexbox `justify-content` utility classes for aligning items horizontally within a row.
// @param $breakpoint-suffix: A string suffix for the breakpoint (e.g., '', '-sm', '-md').
@mixin generate-justify-content($breakpoint-suffix: '') {
  .justify-content#{$breakpoint-suffix}-start {
    justify-content: flex-start;
  }
  .justify-content#{$breakpoint-suffix}-end {
    justify-content: flex-end;
  }
  .justify-content#{$breakpoint-suffix}-center {
    justify-content: center;
  }
  .justify-content#{$breakpoint-suffix}-between {
    justify-content: space-between;
  }
  .justify-content#{$breakpoint-suffix}-around {
    justify-content: space-around;
  }
  .justify-content#{$breakpoint-suffix}-evenly {
    justify-content: space-evenly;
  }
}

// Generates flexbox `align-items` utility classes for aligning items vertically within a row.
// @param $breakpoint-suffix: A string suffix for the breakpoint (e.g., '', '-sm', '-md').
@mixin generate-align-items($breakpoint-suffix: '') {
  .align-items#{$breakpoint-suffix}-normal {
    align-items: normal;
  }
  .align-items#{$breakpoint-suffix}-start {
    align-items: flex-start;
  }
  .align-items#{$breakpoint-suffix}-end {
    align-items: flex-end;
  }
  .align-items#{$breakpoint-suffix}-center {
    align-items: center;
  }
  .align-items#{$breakpoint-suffix}-baseline {
    align-items: baseline;
  }
  .align-items#{$breakpoint-suffix}-stretch {
    align-items: stretch;
  }
}

// Generates flexbox `align-self` utility classes for aligning individual items vertically within a row.
// @param $breakpoint-suffix: A string suffix for the breakpoint (e.g., '', '-sm', '-md').
@mixin generate-align-self($breakpoint-suffix: '') {
  .align-self#{$breakpoint-suffix}-start {
    align-self: flex-start;
  }
  .align-self#{$breakpoint-suffix}-end {
    align-self: flex-end;
  }
  .align-self#{$breakpoint-suffix}-center {
    align-self: center;
  }
  .align-self#{$breakpoint-suffix}-baseline {
    align-self: baseline;
  }
  .align-self#{$breakpoint-suffix}-stretch {
    align-self: stretch;
  }
}

// Grid Container
.row {
  @include make-row;
}
.col {
  flex: 1 0 0;
}
[class*='col-'] {
  @include make-col-base;
}
// Generate base (mobile-first) column and alignment classes (no breakpoint suffix).
// These classes apply to all screen sizes unless overridden by a larger breakpoint.
@include generate-column-classes;
@include generate-justify-content;
@include generate-align-items;
@include generate-align-self;
@include generate-order-classes;
@include generate-flex-grow;
// Iterate through defined breakpoints to generate responsive classes.
// For each breakpoint, media queries ensure these styles apply only from that size upwards.
@each $name, $size in $mq {
  @media #{$size} {
    // Generate column classes for the current breakpoint (e.g., .col-md-6, .offset-lg-2)
    @include generate-column-classes('-#{$name}');

    // Generate alignment classes for the current breakpoint (e.g., .justify-content-md-center)
    @include generate-justify-content('-#{$name}');
    @include generate-align-items('-#{$name}');
    @include generate-align-self('-#{$name}');

    // Generate order classes for the current breakpoint (e.g., .order-md-2, .order-lg-first)
    @include generate-order-classes('-#{$name}');
    @include generate-flex-grow('-#{$name}');
  }
}

// --- Helper for clearing floats (optional, but good practice for mixed content) ---
// This utility can be used on a parent element to clear floats of its children,
// though less common with flexbox layouts.
.clearfix::after {
  content: '';
  display: table;
  clear: both;
}
