@use './imports' as *;
@use 'sass:map';
@use 'sass:color';

:root {
  --border-width: #{px-to-rem(1px)};
  @each $key, $variants in $theme-colors {
    $light-value: map.get($variants, light);
    $dark-value: map.get($variants, dark);
    .light-mode {
      --color-#{$key}: #{$light-value};

      --color-bg-#{$key}: #{color.adjust($dark-value, $lightness: 20%)};
      --color-bg-dot-#{$key}: #{color.adjust($dark-value, $lightness: 10%)};
      --color-border-#{$key}: #{color.adjust($dark-value, $lightness: 10%)};
      --color-bg-semi-#{$key}: #{rgba($light-value, 90%)};
      --color-bg-invert-#{$key}: #{color.adjust($dark-value, $lightness: 20%)};
    }
    .dark-mode {
      --color-#{$key}: #{$dark-value};

      --color-bg-#{$key}: #{color.adjust($light-value, $lightness: -20%)};
      --color-bg-dot-#{$key}: #{color.adjust($light-value, $lightness: -10%)};
      --color-border-#{$key}: #{color.adjust($light-value, $lightness: -10%)};
      --color-bg-semi-#{$key}: #{rgba($dark-value, 90%)};
      --color-bg-invert-#{$key}: #{color.adjust($light-value, $lightness: -20%)};
    }
  }
  .light-mode {
    --color-body-bg: #{map.get($grays, 'gray-2')};
    --color-text: #{map.get($grays, 'gray-8')};
    --color-headings: #{color.adjust(map.get($grays, 'gray-8'), $lightness: -20%)};
    --color-small: #{map.get($grays, 'gray-6')};
    --color-link: #{map.get($map: map.get($theme-colors, 'primary'), $key: 'light')};
    --color-link-hover: #{color.adjust(
        map.get($map: map.get($theme-colors, 'primary'), $key: 'light'),
        $lightness: -10%
      )};
    @each $key, $variant in $grays {
      --color-#{$key}: #{$variant};
      --color-bg-dot-#{$key}: #{color.adjust($variant, $lightness: 10%)};
      --color-bg-#{$key}: #{$variant};
      --color-bg-semi-#{$key}: #{rgba($variant, 85%)};
    }
    --color-bg-semi-best: #{rgba(map.get($grays, 'gray-0'), 85%)};
    --color-active-state: #{rgba(map.get($grays, 'gray-10'), 15%)};
  }
  .dark-mode {
    --color-body-bg: #{map.get($grays, 'gray-8')};
    --color-text: #{map.get($grays, 'gray-2')};
    --color-headings: #{color.adjust(map.get($grays, 'gray-2'), $lightness: 20%)};
    --color-small: #{map.get($grays, 'gray-4')};
    --color-link: #{map.get($map: map.get($theme-colors, 'primary'), $key: 'dark')};
    --color-link-hover: #{color.adjust(
        map.get($map: map.get($theme-colors, 'primary'), $key: 'dark'),
        $lightness: 10%
      )};
    @each $key, $variant in $grays-dark {
      --color-#{$key}: #{$variant};
      --color-bg-dot-#{$key}: #{color.adjust($variant, $lightness: -10%)};
      --color-bg-#{$key}: #{$variant};
      --color-bg-semi-#{$key}: #{rgba($variant, 85%)};
    }
    --color-bg-semi-best: #{rgba(map.get($grays, 'gray-10'), 85%)};
    --color-active-state: #{rgba(map.get($grays, 'gray-0'), 15%)};
  }
}

*,
::after,
::before {
  box-sizing: border-box;
}

* {
  &::after,
  &::before {
    box-sizing: border-box;
  }
}

html,
body {
  scroll-behavior: smooth;
}

html {
  block-size: 100%;
}

body {
  min-block-size: 100%;
  background-color: var(--color-body-bg);
  color: var(--color-text);

  p {
    color: var(--color-text);
  }

  main {
    padding-top: calc(48px + 4px);
    @include bpxl {
      padding-top: calc(72px + 4px);
    }
  }

  &.home,
  &.detail {
    main {
      padding-top: 0;
    }
  }
}

.display-1,
.display-2,
.display-3,
.display-4,
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6,
label {
  color: var(--color-headings);
}
small {
  color: var(--color-small);
}

.brand-bordered-bottom {
  border-bottom: #{map.get($gap-sizes, 1)} solid var(--color-primary);
}
.brand-bordered-top {
  border-top: #{map.get($gap-sizes, 1)} solid var(--color-primary);
}

hr {
  border-bottom: 0;
  border-top: var(--border-width) solid var(--color-gray-5);
  height: 0;
}

img {
  vertical-align: top;
}

.container {
  @include containerize;
  max-width: $container-center-width;
}
.container-max {
  @include containerize;
  max-width: $container-max-width;
  padding: 0 !important;
}

.section-title {
  align-items: center;
  color: var(--color-primary);
  display: flex;
  gap: map.get($gap-sizes, 5);
  justify-content: center;
  text-align: center;
  &::before,
  &::after {
    background-color: var(--color-primary);
    content: '';
    display: block;
    height: var(--border-width);
    width: map.get($gap-sizes, 9);
  }
  @include bpmd {
    gap: map.get($gap-sizes, 6);
    &::before,
    &::after {
      width: map.get($gap-sizes, 11);
    }
  }
  @include bpxl {
    gap: map.get($gap-sizes, 7);
  }
}
a {
  text-decoration: underline;
}
.list-unstyled {
  list-style: none !important;
}
